import React, { Component } from 'react';
import {Badge, Button} from 'react-bootstrap';

function steelIteration(Mu, fc, fy, bw, d, phi){
    
    const n_iter = 50;
    let a = 0.2*12;
    let As;

    for (let i=0; i<n_iter;i++){
        As = Mu/(phi * fy * (d-a/2));
        a = As*fy/(0.85 * fc * bw)
    }
    return [As, a]
}

class SteelIteration extends Component {
    state = {  } ;

    constructor(props) {
        super(props);
        console.log(this.props.Mu)
      }

    stateChange = (f) => {
        const {name, value} = f.target;
        this.setState({
          [name]: value,
        });
    }
    
    sstate = steelIteration(this.props.Mu, this.props.fc, this.props.fy, 
        this.props.bw, this.props.d, this.props.phi);


    render() { 
        return (
            <div>
                
                <br></br>
                <Badge pill bg="primary">
                Area of steel = { this.sstate[1]} in^2
                </Badge>
                <br></br>
                <Badge pill bg="primary">
                Compression Block Depth = {  this.sstate[0] } in 
                </Badge>
                
            </div>
        );
    }
}


export default SteelIteration;


