
import React, { Component } from 'react'
import {Nav, Navbar, Container} from 'react-bootstrap';

import './navigationBar.css';

class NavigationBar extends Component {
    state = {  } 

    constructor(props){
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.myRef = React.createRef();
    }
    
    handleClick(){
        if (this.myRef.current.className === "topnav") {
            this.myRef.current.className += " responsive";
          } else {
            this.myRef.current.className = "topnav";
          }

    };

    render() { 
        return (
          
            <Navbar ref={this.myRef} className="topnav" bg="primary" variant="dark">
                <Container>
                    <Navbar.Brand href="/">Steel Iteration</Navbar.Brand>
                    <Nav className="me-auto">
                    <Nav.Link href="/help">Help</Nav.Link>
                    <Nav.Link href="/about">About the Author</Nav.Link>
                    <a className="icon" onClick={{}} />
                    </Nav>       
                </Container>  
            </Navbar>
 
        );
    }
}
 
export default NavigationBar;