import React, { Component} from 'react';

import SteelIteration from './steelIteration';
import MathJax from 'react-mathjax';
import 'bootstrap/dist/css/bootstrap.min.css';
import ReactGA from "react-ga4";

import './App.css';
import NavigationBar from './navigationBar'

import {Card, Badge, Button, Form, FloatingLabel} from 'react-bootstrap';

//import concrete_beam from './concrete-beam.png';
import "./steelIterationUI.scss";

ReactGA.initialize("G-TCW1B9FQMY");
ReactGA.send("pageview");

function steelIteration(Mu, fc, fy, bw, d, phi){
    
    const n_iter = 10;
    let a = 0.2*12;
    let As;

    for (let i=0; i<n_iter;i++){
        As = Mu/(phi * fy * (d-a/2));
        a = As*fy/(0.85 * fc * bw)
    }
    return [As, a]
}

class SteelIterationUI extends Component {
    state = {  } 

    constructor(props){
        super(props);
        
        this.state = {
          Mu : 200,
          fc : 3,
          fy : 60,
          bw : 12,
          d  : 9,
          phi:0.9,
          A:0,
          a:0,
          answer:[0,0]
        }

        this.updateInput = this.updateInput.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleClick();
    }

    componentDidMount(){
      this.handleClick();
      document.title = "Steel Iteration"
      
    }

    updateInput(evt) {
      const value = evt.target.value;

      this.setState({

          ...this.state,

          [evt.target.name]: parseFloat(value)

      },
      this.handleClick
      );
        
        
      this.state.answer = steelIteration(this.state.Mu, this.state.fc, this.state.fy, 
            this.state.bw, this.state.d, this.state.phi);
      
      let As, a;
      [As, a] = this.state.answer;
      this.setState({A:As})
      this.setState({a:a})

      
      //this.setState({ answer: { ...this.state.answer, flag: [0,0]} });
  
      
        
        /*
        this.setState({
            answer:steelIteration(this.state.Mu, this.state.fc, this.state.fy, 
                this.state.bw, this.state.d, this.state.phi)
            }
        );*/
      //console.log(this.state.A, this.state.Mu, this.state.answer[0]) 
      
      }

    handleClick(){
      let A,a;
      [A, a] = steelIteration(this.state.Mu, this.state.fc, this.state.fy, 
        this.state.bw, this.state.d, this.state.phi);
      this.setState ({
        Mu : this.state.Mu,
        fc : this.state.fc,
        fy : this.state.fy,
        bw : this.state.bw,
        d  : this.state.d,
        phi:this.state.phi,
        A:A,
        a:a,
        answer:steelIteration(this.state.Mu, this.state.fc, this.state.fy, 
          this.state.bw, this.state.d, this.state.phi)

      });

      //console.log(this.state.A, this.state.Mu, this.state.answer[0]) 
    }
    
    render() { 
        return (
        <div>
          <NavigationBar/>
        
        <div style={{position: 'relative',paddingLeft: "5vw", paddingTop:"3vh",margin:'auto'}}>
          
          <Card style={{ width: '90vw' }}>
            
          <h2 style={{display:'flex', justifyContent:'center'}}>
          Area of Steel 
          </h2>
          <div className="concrete-beam"></div>

          <MathJax.Provider style={{display:"block"}}>
            <MathJax.Node formula="A_s = \frac{M_u}{\phi f_y (d- \frac{a}{2})}"/>
            <MathJax.Node formula="a = \frac{A_s}{0.85 f_c' b_w}"/>
          </MathJax.Provider>
       
        <FloatingLabel controlId="Mu" label="Mu (kip-in)" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="Mu" type="number" defaultValue="200" />
        </FloatingLabel>

        <FloatingLabel controlId="fc" label="fc (ksi)" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="fc" type="number" defaultValue="3" />
        </FloatingLabel>

        <FloatingLabel controlId="fy" label="fy (ksi)" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="fy" type="number" defaultValue="60" />
        </FloatingLabel>

        <FloatingLabel controlId="bw" label="bw (in)" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="bw" type="number" defaultValue="12" />
        </FloatingLabel>

        <FloatingLabel controlId="d" label="d (in)" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="d" type="number" defaultValue="9" />
        </FloatingLabel>

        <FloatingLabel controlId="phi" label="phi" className="mb-3" onChange={this.updateInput}>
          <Form.Control name="phi" type="number" defaultValue="0.9" />
        </FloatingLabel>
        

        <Button variant="primary" type="submit" onClick={this.handleClick}> Calculate </Button>

        <div>
                
            <br></br>
            <MathJax.Provider style={{display:"block"}}>
              <Badge pill className="output" bg="primary" style={{fontSize:15,display:'flex', justifyContent:'center'}}>
                <div style={{position:'relative'}}>
                Area of steel = { this.state.answer[0].toFixed(4)} <MathJax.Node inline formula="\text{in}^2"/>
                <br/>
                <div style={{}}>
                Compression Block Depth = {  this.state.answer[1].toFixed(4)} in 
                </div>
                </div>
              
              </Badge>
            </MathJax.Provider>
                
        </div>
        </Card>

       


        </div>
        {/*<SteelIteration Mu={this.state.Mu} fc={3} fy={60} bw={12} d={8} phi={0.9}/>*/}
        
        

        </div>
        );
    }
}
 
export default SteelIterationUI;